.no-webp .imagen-datos{
    background-image: url(../img/encuentra.jpg);
}
.webp .imagen-datos{
    background-image: url(../img/encuentra.webp);
}
@include tablet{
    .contenedor-datos{
        overflow: hidden;
        h3{
            font-size: 1.5rem;
            font-weight: bold;
            
        }
        @include grid(4,2rem);
        
    }
}
.imagen-datos{
    height: 50rem;
    background-position: center center;
    background-size: cover;
    padding: $separacion 0;
    display: flex;
    flex-direction: column;
    align-items: center;
}
.texto-datos{
    font: $fuentesDetalles;
    color: $blanco;
    font-weight: bold;
    h2{
        margin-top: 0;
        margin-bottom: 0;
    }

}
.datos {
    background-color: $blanco;
    border-radius: 2rem;
}
.intro-dato{
    .datito{
        margin-bottom: 0;
    }
    h3{
        color: $blanco;
        margin-top: 0;
    }
}
@include tablet {
    .contenedor-datos {
        .datos {
            height: 20rem; // Ajusta la altura seg√∫n sea necesario
            width: 40rem; // Ajusta el ancho seg√∫n sea necesario
        }
    }
}
.info-dato{
    text-align: center;
    p.digito-dato{
        font-weight: bold;
        margin-top: 0;
        margin-bottom: 0;
        color: $azul;
        font-size: 7rem;
    }
    .texto-dato{
        text-transform: uppercase;
        margin-top: 0;
        color: $grisOscuro;
        font-size: 2rem;
        margin-left: 2rem;
        margin-right: 2rem;
    }
}