@include tablet{
    .contenedor-comentario{
        margin-right: 5rem;
        margin-left: 5rem;
        overflow: auto;
        h3{
            font-size: 1.5rem;
            font-weight: bold;
            
        }
        img{
            .contenedor-comentario:last-child {
                margin-left: 2rem; /* Elimina el margen derecho del √∫ltimo elemento para evitar espacio extra */
            }
        }
        @include grid(4,2rem);
        
    }
}

.comentarios{
    overflow: hidden;
    position: relative;
    background-color: $blanco;
    padding: 2rem;
    border-radius: 2rem;
    .contenido-comentarios{
        padding: 2rem;
        
    }
    h3{
        display: flex;
        justify-content: flex-start;
        margin: 0;
        color: $casa_sub;
        font-size: 2.5rem;
    }
    img{
        width: 40rem;
        border-radius: 2rem;
    }

}
.descripcion-comentario{
    display: flex;
    align-items: center;
    justify-content: space-between;
    margin-bottom: 2rem;

}
.fecha-comentario{
    color: $grisOscuro;
    text-align: center;
    font-size: 2rem;
    margin-bottom: 0;
    margin-top: 0;
}
.autor-comentario{
    color: $grisOscuro;
    font-weight: $regular;
    font-size: 2.5rem;
    text-align: center;
    margin-top: 0;
    margin-bottom: 0;

}