body {
  background-color: #ebe7f3;
  font-family: "Noto Sans", sans-serif;
  font-size: 1.6rem;
  line-height: 1.8;
}

p {
  color: #FFFFFF;
  font-size: 2rem;
}

.contenedor {
  width: 95%;
  max-width: 150rem;
  margin: 0 auto;
}

a {
  text-decoration: none;
}

img,
picture {
  width: 100%;
  display: block;
}

h1, h2, h3 {
  font-family: "Poppins", sans-serif;
  margin: 2.5rem 0;
  font-weight: 300;
  text-align: center;
}

h1 {
  font-size: 3.8rem;
}

h2 {
  font-size: 3.4rem;
}

h3 {
  font-size: 3rem;
}

h4 {
  font-size: 2.6rem;
}

.seccion {
  margin-top: 2.5rem;
  margin-bottom: 2.5rem;
}

.seccion-dos {
  margin-top: 2.5rem;
  margin-bottom: 2.5rem;
}

.contenido-centrado {
  max-width: 800px;
}

.boton-tipo {
  background-color: #34216b;
  color: #FFFFFF;
  font-weight: 700;
  text-decoration: none;
  border-radius: 2rem;
  display: flex;
  flex-direction: column;
  padding: 1rem 2rem;
  text-align: center;
  margin-top: 2.5rem;
  display: block;
}

.boton-mas {
  background-color: #34216b;
  color: #FFFFFF;
  font-weight: 700;
  text-decoration: none;
  border-radius: 2rem;
  display: flex;
  flex-direction: column;
  padding: 1rem 2rem;
  text-align: center;
  margin-top: 2.5rem;
  display: inline-block;
}

.boton-mas-enviar {
  background-color: #34216b;
  color: #FFFFFF;
  font-weight: 700;
  text-decoration: none;
  border-radius: 2rem;
  display: flex;
  flex-direction: column;
  padding: 1rem 2rem;
  text-align: center;
  margin-top: 2.5rem;
  display: inline-block;
}

.boton-mas_cuarto {
  background-color: #34216b;
  color: #FFFFFF;
  font-weight: 700;
  text-decoration: none;
  border-radius: 2rem;
  display: flex;
  flex-direction: column;
  padding: 1rem 2rem;
  text-align: center;
  margin-top: 2.5rem;
  display: inline-block;
}

.boton-tipo_region {
  background-color: #ebe7f3;
  color: #FFFFFF;
  font-weight: 700;
  text-decoration: none;
  border-radius: 2rem;
  display: flex;
  flex-direction: column;
  padding: 1rem 2rem;
  text-align: center;
  margin-top: 2.5rem;
  display: block;
}

.boton-acceder {
  background-color: #d854a5;
  color: #FFFFFF;
  font-weight: 700;
  text-decoration: none;
  border-radius: 2rem;
  display: flex;
  flex-direction: column;
  padding: 1rem 2rem;
  text-align: center;
  margin-top: 2.5rem;
  display: inline-block;
}

/**UI**/
.header {
  background-color: #34216b;
  padding: 2rem;
}

.logo-micasita {
  height: 5rem;
}

.footer {
  padding: 2.5rem;
  background-color: #34216b;
  margin-bottom: 0;
}
.footer .contenedor-footer {
  padding-top: 2.5rem;
}
.footer .copyright {
  color: #FFFFFF;
  text-align: center;
}

.barra {
  display: flex;
  flex-direction: column;
}
@media (min-width: 768px) {
  .barra {
    flex-direction: row;
    justify-content: space-between;
  }
}

.navegacion a:hover {
  color: #d854a5;
}
.navegacion .boton-acceder {
  margin: 0;
}
.navegacion .boton-acceder a:hover {
  color: #34216b;
}
@media (min-width: 768px) {
  .navegacion {
    display: flex;
    align-items: center;
  }
}
.navegacion a {
  color: #FFFFFF;
  text-decoration: none;
  font-size: 2.2rem;
  display: block;
  text-align: center;
}
@media (min-width: 768px) {
  .navegacion a {
    margin-right: 2rem;
    font-size: 1.8rem;
  }
  .navegacion a:last-of-type {
    margin-right: 0;
  }
}

.fotos {
  position: relative;
  max-height: 70rem;
  overflow: hidden;
}
.fotos .slide {
  position: absolute;
  height: 100%;
  width: 100%;
  display: flex;
  flex-direction: column;
  justify-content: center;
  background-color: rgba(0, 0, 0, 0.5);
}
.fotos img {
  width: 100%;
  display: block;
}

.contenido-foto {
  opacity: 1;
}
.contenido-foto h2 {
  display: flex;
  justify-content: left;
  color: #FFFFFF;
  text-transform: uppercase;
  font-weight: bold;
  font-size: 20px;
}
.contenido-foto .titulo {
  font-weight: normal;
  font-size: 30px;
}
.contenido-foto .titulo p {
  margin: 0;
  font-size: 50px;
}
.contenido-foto p {
  padding-top: 0;
  display: flex;
  justify-content: left;
  text-transform: uppercase;
}

@media (min-width: 768px) {
  .contenedor-casas {
    overflow: auto;
    display: grid;
    gap: 2rem;
    grid-template-columns: repeat(4, 1fr);
  }
  .contenedor-casas h3 {
    font-size: 1.5rem;
    font-weight: bold;
  }
}
.casas {
  position: relative;
  background-color: #ebe7f3;
  padding: 2rem;
  border-radius: 2rem;
}
.casas .contenido-casa {
  padding: 2rem;
}
.casas h3 {
  margin: 0;
  color: #d854a5;
}

.boton-tipo {
  position: absolute;
  width: 40%;
  margin: 0.2rem;
  margin-top: 0.6rem;
  padding: 0.1em 0.1rem;
}
@media (min-width: 768px) {
  .boton-tipo {
    display: flex;
    width: 85%;
  }
}

.boton-mas {
  display: flex;
  justify-content: center;
  width: auto;
  align-items: center;
  margin: 0 auto;
}
@media (min-width: 768px) {
  .boton-mas {
    display: flex;
    width: 70%;
  }
}
.boton-mas:hover {
  background-color: #d33f9a;
}

.titulo-precio {
  color: #455CEB;
  text-align: center;
  font-size: 2rem;
  border-top: 4px solid #e1e1e1;
  margin-bottom: 0;
}

.precio {
  color: #333333;
  font-weight: bold;
  font-size: 2.5rem;
  text-align: center;
  margin-top: 0;
}

.seccion-contenedor {
  display: block;
}
@media (min-width: 1024px) {
  .seccion-contenedor {
    display: flex;
    flex-direction: column;
    align-items: center;
  }
}
.seccion-contenedor h1 {
  font: "Raleway", sans-serif;
  font-weight: bold;
  color: #d854a5;
}

.marco {
  background-color: #FFFFFF;
  border-radius: 3rem;
  padding: 2rem;
  display: flex;
  margin-bottom: 2rem;
  flex-wrap: wrap;
  gap: 2rem;
  overflow: auto;
}
@media (min-width: 768px) {
  .marco {
    width: 136rem;
    margin-bottom: auto;
  }
}
@media (min-width: 768px) {
  .marco {
    max-width: 100%;
  }
}

.iconos-caracteristicas {
  display: flex;
  list-style: none;
  padding: 0;
  align-items: center;
}
.iconos-caracteristicas li {
  display: flex;
  flex-direction: column;
  align-items: center;
  padding: 1.2rem;
}
.iconos-caracteristicas li p {
  font-size: 1.4rem;
  font-weight: bold;
  margin: 0;
  color: #34216b;
}
.iconos-caracteristicas li img {
  width: 5rem;
  height: 5rem;
  margin-bottom: 0.1rem;
}

.seccion-dos {
  width: 80%;
  margin-top: 5rem;
  margin-bottom: 5rem;
}

@media (min-width: 768px) {
  .contenedor-cuarto {
    overflow: hidden;
    display: grid;
    gap: 2rem;
    grid-template-columns: repeat(3, 1fr);
  }
  .contenedor-cuarto h2 {
    font-size: 1.5rem;
    font-weight: bold;
  }
  .contenedor-cuarto img {
    display: block;
    margin: 0 auto;
  }
  .contenedor-cuarto picture {
    margin-top: 2rem;
  }
}
.cuarto-total img {
  height: 11rem;
  padding-top: 1rem;
  margin: 0 auto;
  border-radius: 10rem;
}

.titulo-cuarto {
  color: #34216b;
  font-weight: bold;
  margin-bottom: 0;
  text-align: left;
  margin-top: 2rem;
}

.descripcion-cuarto {
  color: #333333;
  margin-top: 0;
  text-align: justify;
  font-size: 20px;
  margin-bottom: 0;
  font-weight: 400;
}

@media (min-width: 768px) {
  .contenido-texto {
    display: flex;
    flex-direction: column;
  }
}
.boton-mas_cuarto {
  background-color: #FFFFFF;
  color: #34216b;
  border: 2px solid #34216b;
  display: flex;
  justify-content: center;
  width: auto;
  align-items: center;
  margin: 0 auto;
  width: 20rem;
}
@media (min-width: 768px) {
  .boton-mas_cuarto {
    display: flex;
    width: 70%;
  }
}
.boton-mas_cuarto:hover {
  background-color: #d64ca1;
}

@media (min-width: 768px) {
  .contenido, .seccion-dos {
    display: grid;
    gap: 2rem;
    grid-template-columns: repeat(2, 1fr);
  }
}
.cuarto {
  height: 25rem;
  background-color: #FFFFFF;
  max-width: auto;
  max-height: auto;
  border-radius: 2rem;
}
@media (min-width: 768px) {
  .cuarto img {
    display: flex;
    justify-content: center;
    align-items: center;
    margin-top: 1rem;
    height: auto;
    width: 13rem;
    border-radius: 20rem;
  }
}
.cuarto h3 {
  font-size: 2rem;
  font-weight: 400;
  color: #34216b;
  margin-top: 1rem;
}
.cuarto img {
  width: 12rem;
}

@media (min-width: 768px) {
  .contenedor-regiones {
    margin-left: 8rem;
    margin-right: 8rem;
    overflow: auto;
    height: 100rem;
    display: grid;
    gap: 2rem;
    grid-template-columns: repeat(4, 1fr);
  }
  .contenedor-regiones h3 {
    font-size: 1.5rem;
    font-weight: bold;
  }
  .cuadro-region {
    height: 113rem;
  }
}
.cuadro-region {
  height: 100% rem;
  background-color: #34216b;
  border-radius: 0.6rem;
}
.cuadro-region h2 {
  color: #FFFFFF;
  font-weight: bold;
  font-size: 40px;
}

.region {
  overflow: hidden;
  margin: 1rem;
  margin-bottom: 0;
}
.region a {
  color: #34216b;
}
.region img {
  border-radius: 2rem;
}
.region h3 {
  color: #FFFFFF;
}

.abajo {
  max-height: auto;
  grid-row: 2/3;
}

.contenido-region {
  margin-top: 0;
  margin-bottom: 2rem;
}
.contenido-region h3 {
  text-align: left;
  margin: 0;
}

.boton-tipo_region {
  width: 40%;
  margin: 0.2rem;
  margin-top: 0.6rem;
  padding: 0.1em 0.1rem;
}
@media (min-width: 768px) {
  .boton-tipo_region {
    display: flex;
    width: 70%;
    position: relative;
  }
}

.region-grande {
  overflow: hidden;
  margin: 1rem;
  grid-row: 2/3;
  grid-column: 1/3;
}
.region-grande a {
  color: #34216b;
}
.region-grande img {
  height: 40rem;
  border-radius: 2rem;
}
.region-grande h3 {
  color: #FFFFFF;
}
.region-grande .boton-tipo_region {
  width: 40%;
  margin: 0.2rem;
  margin-top: 0.6rem;
  padding: 0.1em 0.1rem;
}
@media (min-width: 768px) {
  .region-grande .boton-tipo_region {
    display: flex;
    width: 30%;
    position: relative;
  }
}

.region-largo {
  overflow: hidden;
  margin: 1rem;
  grid-row: 1/2;
  grid-column: 3/5;
}
.region-largo .boton-tipo_region {
  width: 40%;
  margin: 0.2rem;
  margin-top: 0.6rem;
  padding: 0.1em 0.1rem;
}
@media (min-width: 768px) {
  .region-largo .boton-tipo_region {
    display: flex;
    width: 30%;
    position: relative;
  }
}
.region-largo a {
  color: #34216b;
}
.region-largo img {
  border-radius: 2rem;
  height: 40rem;
}
.region-largo h3 {
  color: #FFFFFF;
}

.contenido-nosotros {
  overflow: hidden;
}
@media (min-width: 768px) {
  .contenido-nosotros {
    display: grid;
    gap: 2rem;
    grid-template-columns: repeat(2, 1fr);
  }
}
.contenido-nosotros img {
  height: 50rem;
  width: 80rem;
  border-radius: 2rem;
  margin-left: 1rem;
}

.nosotros-texto {
  padding-bottom: 2rem;
}
.nosotros-texto h1 {
  text-align: start;
  color: #34216b;
  font-size: "Raleway", sans-serif;
  font-weight: bold;
  margin-bottom: 0;
}
.nosotros-texto p {
  margin-top: 0;
  text-align: justify;
  color: #333333;
}

.boton-mas {
  width: 15rem;
  display: inline-block;
}

.no-webp .imagen-datos {
  background-image: url(../img/encuentra.jpg);
}

.webp .imagen-datos {
  background-image: url(../img/encuentra.webp);
}

@media (min-width: 768px) {
  .contenedor-datos {
    overflow: hidden;
    display: grid;
    gap: 2rem;
    grid-template-columns: repeat(4, 1fr);
  }
  .contenedor-datos h3 {
    font-size: 1.5rem;
    font-weight: bold;
  }
}
.imagen-datos {
  height: 50rem;
  background-position: center center;
  background-size: cover;
  padding: 5rem 0;
  display: flex;
  flex-direction: column;
  align-items: center;
}

.texto-datos {
  font: "Raleway", sans-serif;
  color: #FFFFFF;
  font-weight: bold;
}
.texto-datos h2 {
  margin-top: 0;
  margin-bottom: 0;
}

.datos {
  background-color: #FFFFFF;
  border-radius: 2rem;
}

.intro-dato .datito {
  margin-bottom: 0;
}
.intro-dato h3 {
  color: #FFFFFF;
  margin-top: 0;
}

@media (min-width: 768px) {
  .contenedor-datos .datos {
    height: 20rem;
    width: 40rem;
  }
}
.info-dato {
  text-align: center;
}
.info-dato p.digito-dato {
