.barra{
    display: flex;
    flex-direction: column;
    @include tablet {
        flex-direction: row;
        justify-content: space-between;
    }
}

.navegacion{
    
    a:hover {
        color: $rosa;
    }
    .boton-acceder{    
        a:hover {
            color: $azul;
        }
        margin: 0;
    }
    @include tablet {
        display: flex;
        align-items: center;
    }
    
    a{
        color: $blanco;
        text-decoration: none;
        font-size: 2.2rem;
        display: block;
        text-align: center;

        @include tablet {
            margin-right: 2rem;
            font-size: 1.8rem;
            &:last-of-type{
                margin-right: 0;
            }
        }
        
    }
    
}
.boton,.boton-acceder{

}