@include tablet{
    .contenedor-regiones{
        margin-left: 8rem;
        margin-right: 8rem;
        overflow: auto;
        height: 100rem;
        h3{
            font-size: 1.5rem;
            font-weight: bold;
            
        }
        @include grid(4,2rem);
        
    }
    .cuadro-region{
    
        height: 113rem;
    }
}
.cuadro-region{
    
    height: 100%rem;
    h2{
        color: $blanco;
        font-weight: bold;
        font-size: 40px;
    }
    background-color: $azul;
    border-radius: 0.6rem;
}
.region{
    overflow:hidden;
    margin: 1rem;
    margin-bottom: 0;
    a{
        color: $azul;
    }
    img{
        border-radius: 2rem;
        
    }
    h3{
        color: $blanco;
    }
}
.abajo{
    max-height: auto;
    grid-row: 2/3;
}
.contenido-region{
    
    margin-top: 0;
    margin-bottom: 2rem;
    h3{
        text-align: left;
        margin: 0;
    }
}
.boton-tipo_region{

    @include tablet{
        display: flex;
        width: 70%;
        position: relative;    
    }
    
    width: 40%;
    margin: 0.2rem;
    margin-top: 0.6rem;
    padding: 0.1em 0.1rem;
    
    
}
.region-grande{
    overflow:hidden;
    margin: 1rem;
    a{
        color: $azul;
    }
    img{
        height: 40rem;
        border-radius: 2rem;
    }
    h3{
        color: $blanco;
    }
    .boton-tipo_region{

        @include tablet{
            display: flex;
            width: 30%;
            position: relative;    
        }
        
        width: 40%;
        margin: 0.2rem;
        margin-top: 0.6rem;
        padding: 0.1em 0.1rem;
        
        
    }
    grid-row: 2/3;
    grid-column: 1/3;
}
.region-largo{
    overflow:hidden;
    margin: 1rem;
    .boton-tipo_region{

        @include tablet{
            display: flex;
            width: 30%;
            position: relative;    
        }
        
        width: 40%;
        margin: 0.2rem;
        margin-top: 0.6rem;
        padding: 0.1em 0.1rem;
        
        
    }
    a{
        color: $azul;
    }
    img{
        border-radius: 2rem;
        height: 40rem;
    }
    h3{
        color: $blanco;
    }
    grid-row: 1/2;
    grid-column: 3/5;
    
}
    
