
.imagen-contacto {
    @include tablet{
    overflow: hidden;
    flex: 1 1 50%; 
    height: 100rem;
    width: 156.4rem;
    margin-left: 55rem; 
    display: flex;
    justify-content: flex-end; 
    }
}
.formulario-container {
    @include tablet{
    position: absolute;
    top: 20rem;
    left: 10rem;
    width: 40%;
    height: 65%;
    padding: 2rem;
    box-sizing: border-box;
    background-color: $azul; 
    border-radius: 10px; 
    }
    p{
        color: $blanco;
        margin-left: 2rem;
        margin-right: 2rem;
    }
    form{
        border-radius: 10px;
        padding: 20px;
        color: $blanco;
        font-weight: bold;
        }
        h2{
            font-family: $fuentesDetalles;
            font-weight: bold;
            color: $blanco;
        }
}
.contenido-contacto{
    @include grid(2,2rem);
    margin-left: 2rem;
    margin-right: 2rem;
}
.formulario-container {
    position: absolute;
    top: 20rem;
    left: 10rem;
    max-width: 50%;
    padding: 2rem;
    box-sizing: border-box;
    background-color: $azul;
    border-radius: 10px;
}

.form-item {
    margin-bottom: 1rem; 
}

.form-item label {
    display: block; 
    margin-bottom: 0.5rem; 
}

.form-item input,
.form-item textarea {
    width: 100%;
    padding: 0.5rem;
    border-radius: 2rem;
    border: 1px solid #ccc;
}
.text-mensaje{
    grid-row: 3/3;
    grid-column: 3/1;
}
.boton-mas-enviar{
    margin-top: 0;
    background-color: $lila;
    color: $azul;
    &:hover{
        background-color: $rosa;
    }
}