.seccion-dos{
    width: 80%;
    margin-top: 5rem;
    margin-bottom: 5rem;
}

@include tablet{
    .contenedor-cuarto{
        overflow: hidden;
        h2{
            font-size: 1.5rem;
            font-weight: bold;
            
        }
        @include grid(3,2rem);
        img{
            display: block;
            margin: 0 auto;
        }
        picture{
            margin-top: 2rem;
        }
        

    }
}

.cuarto-total{
    img{
        
        height: 11rem;
        padding-top: 1rem;
        margin:0 auto;
        border-radius: 10rem;
    }
}
.titulo-cuarto{
        color: $azul;
        font-weight: bold;
        margin-bottom: 0;
        text-align: left;
        margin-top: 2rem;
    }
    .descripcion-cuarto{
        color: $grisOscuro;
        margin-top: 0;
        text-align: justify;
        font-size: 20px;
        margin-bottom: 0;
        font-weight: $regular;
    }
@include tablet{
    .contenido-texto{
        display: flex;
        flex-direction: column;
        
    }
}

.boton-mas_cuarto{
    @include tablet{
        display: flex;
        width: 70%;
    }
    background-color: $blanco;
    color: $azul;
    border: 2px solid $azul;
    display: flex;
    justify-content: center;
    width: auto;
    align-items: center;
    margin: 0 auto;
    &:hover{
        background-color:darken($rosa,2);
    }
    width: 20rem
}

@include tablet{
    .contenido,.seccion-dos{
        @include  grid(2,2rem);
        
    }
    
}

.cuarto{
    @include tablet{
        img{
            display: flex;
            justify-content: center;
            align-items: center;
            margin-top: 1rem;
            height: auto;
            width: 13rem;
            border-radius: 20rem;
        }
    }
    height: 25rem;
    background-color: $blanco;
    max-width: auto;
    max-height: auto;
    border-radius: 2rem;
    h3{
        font-size: 2rem;
        font-weight: $regular;
        color: $azul;
        margin-top: 1rem;
    }
    img{
        width: 12rem;
    }

    
}
