
.comentario-ayuda{
    h1{
        color: $azul;
    }
    p{
        color: $azul;
        padding-left: 10rem;
        padding-right: 10rem;
        margin-bottom: 4rem;
    }
}
.contenedor-ayuda{
    .ayuda{
        .contenido-ayuda{
            overflow: hidden;
            position: relative;
            background-color: $blanco;
            padding: 2rem;
            border-radius: 2rem;
            margin-bottom: 3rem;
            h3{
                font-weight: bold;
            color:$azul;
            display: flex;
            justify-content: flex-start;
            margin: 0;
            font-size: 3rem
            }
        }
    }
}

