.fotos-sobre {
    position: relative;
    max-height: 40rem;
    overflow: hidden;

    .dise√±o-foto-sobre {
        position: absolute;
        height: 100%;
        width: 100%;
        display: flex;
        flex-direction: column;
        justify-content: center;
        background-color: rgba(0, 0, 0, 0.5); 
    }

    img {
        width: 100%;
        display: flex;
    }
}

.contenedor-sobre {
    display: flex;
    justify-content: center;
        
    h1 {
        font-family: $fuenteHeading;
        font-weight: bold;
        color: $blanco;
    }
}

.nosotros-sobre {
    @include tablet{
        
    }
    h1 {
        text-align: start;
        color: $azul;
        font-size: $fuentesDetalles;
        font-weight: bold;
        margin-bottom: 0;
    }
    p {  
        margin-top: 0;
        text-align: justify;
        color: $grisOscuro;
    }
    
    padding-bottom: 2rem;
}

.contenido-sobre {
    overflow: hidden;
    margin-top: 9rem;
    margin-bottom: 5rem;
    background-color: $lila;
    @include tablet {
        .nosotros-sobre {
        
        }
        @include grid(2,2rem);
    }
    img {
        height: 41rem;
        width: 80rem;
        border-radius: 2rem;
        margin-left: 1rem;
    }
}



.color-sobre {
    background-color: $azul;
}

.info-sobre {
    text-align: center;
    .digito-sobre {
        text-align: center;
        font-weight: bold;
        margin-bottom: 0;
        margin-top: 0;
        color: $azul;
        font-size: 2.3rem;
    }
}

.sobrecito {
    font-weight: bold;
    font-size: 6rem;
    color: $blanco;
    margin-bottom: 0;
}

@include tablet{
    .contenedor-sobres{
        overflow: hidden;
        margin-left: 2rem;
        margin-right: 2rem;
        h3{
            font-size: 1.5rem;
            font-weight: bold;
            
        }
        @include grid(3,2rem);
        
    }
}

.sobre {
    background-color: $blanco;
        border-radius: 50%; // Hace que el rect√°ngulo se convierta en un c√≠rculo
        margin-bottom: 2.5rem;
        width: 20rem; // Ajusta el ancho seg√∫n sea necesario
        height: 20rem; // Ajusta la altura seg√∫n sea necesario
        display: flex;
        justify-content: center; // Centra el contenido horizontalmente
        align-items: center;
}

.intro-sobre{
    h3{
        margin-top: 0;
        color: $blanco;
    }
}
@include tablet {
    .contenedor-sobre {
        .datos {
            height: 20rem; // Ajusta la altura seg√∫n sea necesario
            width: 40rem; // Ajusta el ancho seg√∫n sea necesario
        }
    }
}
.info-sobre{
    text-align: center;
    p.texto-sobre{
        
        font-weight: bold;
        margin-top: 0;
        margin-bottom: 0;
        color: $azul;
        font-size: 3rem;
        
    }
}
.final {
    display: flex;
    justify-content: center; 
    align-items: center; 
    margin-top: 6rem;
    margin-bottom: 6rem;
}

.imagen-final{
    img{
        overflow: hidden;
        border-radius: 4rem;
        width: 140rem; 
        height: 50rem; 
    }
    
}